    export default {
        data () {
            return {
                meunList:[],
                openNames: [this.$route.matched[0].name,this.$route.matched[1].name],
                curUserName : sessionStorage.getItem('user').replace(/\"/g, ""),
                modeType: "vertical",
                spanLeft: 5,
                spanRight: 19,
                iconSize:14,
                admin:window.localStorage.getItem('admin'),
                wrapChangePSW: false,//修改密码的弹窗
                resetPSWModalLoading:false,//修改面板的遮罩
                wrapShowInfo:false,  //个人信息弹窗
