                    oldPassword: '',
                    newPassword: '',
                    resetPassword:''
                },
              formValidatePeople:{

              },
                ruleValidate: {
                    oldPassword: [
                        { required: true, message: '密码不能为空', trigger: 'blur' }
                    ],
                    newPassword: [
                        { required: true, message: '密码不能为空', trigger: 'blur' }
                    ],
                    resetPassword: [
                        { required: true, message: '密码不能为空', trigger: 'blur' }
                    ],
                }
            }
        },
        computed: {
        },
      components:{
        ggg:{
          name:'gs',
          template:`
            <div>
             <template v-for="a in msg">
                <Submenu v-if="a.children"  :name="a.name">
                   <template slot="title">
                    <span class="layout-text" >{{a.name}}</span>
                   </template>
                  <template v-for='item in a.children'>
                    <template  v-if="item.children">
                     <Submenu  :name="item.name">
                      <template slot="title">
                        <span class="layout-text" >{{item.name}}</span>
                       </template>
                       <gs :msg='item.children' v-if='item.children' ></gs>
                     </Submenu>
                    </template>
                    <template  v-else >
                      <Menu-item  v-if="!item.hidden" :name="item.path">
                        {{item.name}}
                      </Menu-item >
                     </template>
                   </template>
                </Submenu>
                 <template v-else>
                   <Menu-item :name="a.path"  v-if="!a.hidden">
                      {{a.name}}
                    </Menu-item >
                 </template>
             </template>
            </div>
`,
          props:['msg'],
          data(){
            return {

            }
          }
        }
      },
        mounted(){
          console.log(this.$route.matched)
          if(this.admin=='admin'){
            this.meunList=this.$router.options.routes
          }else if(this.admin=='xingcong'){
            this.meunList= [
              {
                path: '/',
                name: '用户管理',
                iconCls: 'ios-home', //图标样式class
                children: [
                  { path: '/queryPeopleInfo',  iconCls: 'ios-home',name: '用户任务查询'},
                  { path: '/', name: '消费者管理',children: [
                      { path: '/consumer',  name: '消费者管理'},
                    ]},
                  { path: '/', name: '商户管理',children: [
                      { path: '/merchantKey',  name: '商户密钥管理' },
                    ]},
                ]
              },
              {
                path: '/',
                name: '购物订单导出',
                iconCls: 'ios-cart', //图标样式class
                children: [
                  { path: '/yunwei',name: '导出'}
                ]
              },
              {
                path: '/',
                name: '走势图',
                iconCls: 'connection-bars', //图标样式class
                children: [
                  { path: '/chart',name: '互换率走势图'}
                ]
              },
              {
                path: '/',
                name: '统计查询',
                iconCls: 'clipboard', //图标样式class
                children: [
                  { path: '/',
                    name: '积分互换查询',
                    children: [
                      { path: '/exchangeEntrustQuery',  name: '积分互换委托查询' },
                      { path: '/exchangeTurnOverQuery',  name: '积分互换成交查询' },
                      { path: '/breakageQuery',  name: '平台收取折损查询'},
                      { path: '/bindQuery',  name: '积分绑定查询'},
                      { path: '/streamQuery',  name: '积分流水查询' },
                    ]},
                ]
              }
            ];
          }else if(this.admin=='myceshi'){
            this.meunList=[{
              path: '/',
              component: '',
              name: '人员信息',
              iconCls: 'ios-cart', //图标样式class
              children: [
                { path: '/queryPeopleInfo', component: '',name: '人员信息查询'}
              ]
            }]
          }else if(this.admin=='sousoushenbian'){
            this.meunList=[
              {
                path: '/',
                component: '',
                name: '走势图',
                iconCls: 'connection-bars', //图标样式class
                children: [
                  { path: '/chart', component: '',name: '互换率走势图'}
                ]
              },
            ]
          }else if(this.admin=='yunwei'){
            this.meunList=[
              {
                path: '/yunwei',
                component: '',
                name: '运维导出',
                iconCls: 'connection-bars', //图标样式class
                /*children: [
                  { path: '/chart', component: '',name: '互换率走势图'}
                ]*/
              },
            ]
          }
          this.$nextTick(()=>{
            this.$refs.menu.updateOpened()
            this.$refs.menu.updateActiveName()
          })
        },
        methods: {
            //修改密码，展示弹窗
            modifyPassWord() {
                this.wrapChangePSW = true;
                this. formValidate={
                oldPassword: '',
                  newPassword: '',
                  resetPassword:''
              }
            },
            //退出登录
            logout() {
                this.$router.push('/login');
            },
            //展示个人信息
            showInfo(){
                this.wrapShowInfo=true
            },
            //保存个人信息
            saveInfo(){

            },
            //验证保存修改密码
            comfirmModifyPS() {
              let that=this
              this.resetPSWModalLoading=true
                this.$refs.formValidate.validate((valid) => {
                    if (valid) {
                      if(that.formValidate.newPassword!=that.formValidate.resetPassword){
                        that.$Message.error('两次输入的密码不同!')
                        this.resetPSWModalLoading=false
                        return
                      }
                      setTimeout(() => {
                        this.resetPSWModalLoading=false
                        this.wrapChangePSW = false;
                      }, 2000);
                        this.$Message.success('提交成功!');
                    } else {
                        this.$Message.error('表单验证失败!');
                        this.resetPSWModalLoading=false
                        return false;
                    }
                })
            },
